@forward "main";

.profile-image {
  width: 100px; 
  height: 100px;
  border-radius: 50%;
  border: 2px solid #c2dbff;
  display: block;
  margin: 0 auto; /* Center the image horizontally */
}

.blog-name {
  background-color: yellow;
  display: inline-block;
  padding: 2px 5px;
}

/* The sidebar menu */
.sidenav {
    height: 100%; /* Full-height: remove this if you want "auto" height */
    width: 160px; /* Set the width of the sidebar */
    position: fixed; /* Fixed Sidebar (stay in place on scroll) */
    z-index: 1; /* Stay on top */
    top: 0; /* Stay at the top */
    left: 0;
    background-color: white;
    overflow-x: hidden; /* Disable horizontal scroll */
    padding-top: 20px;
    border-right: solid red;
  }
  
  /* The navigation menu links */
  .sidenav a {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 20px;
    color: black;
    display: block;
  }

/* Add unique background colors */
.sidenav a:nth-child(1) {
  background-color: #f8b5b0; /* Light red */
}

.sidenav a:nth-child(2) {
  background-color: #f8deb0;
}

.sidenav a:nth-child(3) {
  background-color: #fafbae;
}

.sidenav a:nth-child(4) {
  background-color: #d5ffa4;
}

.sidenav a:nth-child(5) {
  background-color: #a4fff3;
}

.sidenav a:nth-child(6) {
  background-color: #e5a4ff;
}
  
  /* When you mouse over the navigation links, change their color */
  .sidenav a:hover {
    color: blue;
  }

  nav a.current {
    font-weight: bold; /* Makes current link bolded */
  }
  
  /* Style page content */
  .main {
    margin-left: 150px; /* Same as the width of the sidebar */

    width: 75%;

    background-color: white; /* White background for the text */
    padding: 20px; /* Adds space around the text */
    max-width: 800px; /* Limits the width of the text container */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Optional: Adds a subtle shadow */
    opacity: 0.8;
  }

.sidenav, body {
  background-image: url('/lined-paper/assets/images/lined-paper-background.png');
  background-attachment: fixed; /* Keeps background image fixed while scrolling*/
  }

  /* Home page that displays resent posts */
  .container {
    display: flex;
    max-width: 750px; /* Maximum width to prevent it from growing too large */
    height: auto;
    min-height: 100px;

    list-style-type: none;
    justify-content: center;
    align-items: flex-start;
    
    padding: 5px 14px;
    flex-direction: column;

    margin-left: -36px;
    margin-bottom: 15px;

    flex-shrink: 0;
    border-radius: 4px;
    border: 2px solid #000;
    background-color: #FFF8A7;
  }

  /* On smaller screens, where height is less than 450px, change the style of the sidebar (less padding and a smaller font size) */
  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 18px;}
  }